.Button {
  display: inline-block;
  background: tomato;
  color: whiteSmoke;
  border: 2px solid #aa8888;
  padding: 10px;
  font-size: 16pt;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}

.Button:hover {
  -webkit-box-shadow: 0 0 10px 0px tomato;
          box-shadow: 0 0 10px 0px tomato;
}

.Button:active {
  border: 2px solid white;
  color: white;
}



